import React, { useRef, useEffect, useState } from 'react'
import { motion, useInView, useAnimation } from 'framer-motion'
import {
  sanCau,
  khaitruong,
  huychuong,
  huychuong1,
  thidau,
  thidau1,
  thidau2,
  thidau3,
  thidau4,
  hocsinh,
  hocsinh1,
  hocsinh2,
  hocsinh3,
} from '../../assets'
import { vbgLogo } from '../../assets/icons'
// Hook để xử lý lazy loading animation
const useScrollAnimation = () => {
  const controls = useAnimation()
  const ref = useRef(null)
  const inView = useInView(ref, { once: true, threshold: 0.1 })

  useEffect(() => {
    if (inView) {
      controls.start('visible')
    }
  }, [controls, inView])

  return { ref, controls }
}

// Animation variants
const fadeInUp = {
  hidden: { opacity: 0, y: 60 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.6, ease: 'easeOut' },
  },
}

const fadeInLeft = {
  hidden: { opacity: 0, x: -60 },
  visible: {
    opacity: 1,
    x: 0,
    transition: { duration: 0.6, ease: 'easeOut' },
  },
}

const fadeInRight = {
  hidden: { opacity: 0, x: 60 },
  visible: {
    opacity: 1,
    x: 0,
    transition: { duration: 0.6, ease: 'easeOut' },
  },
}

const staggerContainer = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.2,
      delayChildren: 0.3,
    },
  },
}

// Navbar Component
const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)

  const toggleMenu = () => {
    setIsOpen(!isOpen)
  }

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const navItems = [
    { name: 'Trang chủ', href: '#home' },
    { name: 'Dịch vụ', href: '#services' },
    { name: 'Bảng giá', href: '#pricing' },
    { name: 'Tin tức', href: '#news' },
    { name: 'Liên hệ', href: '#contact' },
  ]

  return (
    <nav
      className={`fixed top-0 z-50 w-full transition-all duration-300 ${
        isScrolled ? 'bg-white shadow-lg' : 'bg-white/90 backdrop-blur-sm'
      }`}
    >
      <div className='mx-auto max-w-7xl px-4'>
        <div className='flex h-18 items-center justify-between sm:h-20 md:h-22 lg:h-24 xl:h-26'>
          {/* Logo */}
          <div className='flex items-center'>
            <img
              src={vbgLogo}
              alt='Vĩnh Bảo Goodminton Logo'
              className='h-8 w-auto object-contain sm:h-8 md:h-12 lg:h-16 xl:h-20'
            />
          </div>

          {/* Desktop Menu */}
          <div className='hidden items-center space-x-8 lg:flex'>
            {navItems.map(item => (
              <a
                key={item.name}
                href={item.href}
                className='group relative font-medium text-gray-700 transition-colors duration-300 hover:text-orange-600'
              >
                {item.name}
                <span className='absolute bottom-0 left-0 h-0.5 w-0 bg-orange-600 transition-all duration-300 group-hover:w-full'></span>
              </a>
            ))}
            <button className='transform rounded-lg bg-orange-600 px-6 py-2 font-semibold text-white transition-all duration-300 hover:scale-105 hover:bg-orange-600'>
              Đặt sân
            </button>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={toggleMenu}
            className='flex h-8 w-8 flex-col items-center justify-center space-y-1 lg:hidden'
          >
            <span
              className={`h-0.5 w-6 bg-gray-700 transition-all duration-300 ease-out ${
                isOpen ? 'translate-y-2 rotate-45' : '-translate-y-0.5'
              }`}
            ></span>
            <span
              className={`h-0.5 w-6 bg-gray-700 transition-all duration-300 ease-out ${
                isOpen ? 'opacity-0' : 'opacity-100'
              }`}
            ></span>
            <span
              className={`h-0.5 w-6 bg-gray-700 transition-all duration-300 ease-out ${
                isOpen ? '-translate-y-2 -rotate-45' : 'translate-y-0.5'
              }`}
            ></span>
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      <div
        className={`fixed h-full w-full bg-white transition-all duration-300 lg:hidden ${
          isOpen
            ? 'top-18 opacity-100 sm:top-20 md:top-22 lg:top-24 xl:top-26'
            : '-top-full opacity-0'
        }`}
      >
        <div className='space-y-2 px-4 py-4'>
          {navItems.map(item => (
            <a
              key={item.name}
              href={item.href}
              onClick={toggleMenu}
              className='block border-b border-gray-100 py-1.5 font-medium text-gray-700 transition-colors duration-300 hover:text-orange-600'
            >
              {item.name}
            </a>
          ))}
          <button className='w-full rounded-lg bg-orange-600 py-2 font-semibold text-white transition-colors duration-300 hover:bg-orange-600'>
            Đặt sân
          </button>
        </div>
      </div>
    </nav>
  )
}

// Hero Section Component with Slider
const HeroSection = () => {
  const { ref, controls } = useScrollAnimation()
  const [currentSlide, setCurrentSlide] = useState(0)

  const slides = [
    {
      title: 'VĨNH BẢO',
      subtitle: 'GOODMINTON',
      description:
        'SÂN CẦU LÔNG HIỆN ĐẠI VỚI ĐẦY ĐỦ DỊCH VỤ: CHO THUÊ SÂN - DỤNG CỤ TẬP LUYỆN VÀ THI ĐẤU, ĐÀO TẠO CHUYÊN NGHIỆP VÀ MUA BÁN TRAO ĐỔI ĐỒ CẦU LÔNG',
      image: thidau4,
      bgGradient: 'from-orange-200 to-white',
    },
    {
      title: 'SÂN CHUẨN',
      subtitle: 'QUỐC TẾ',
      description:
        'SÂN CẦU LÔNG VỚI THẢM CAO CẤP, HỆ THỐNG CHIẾU SÁNG LED CHUYÊN DỤNG VÀ HIỆN ĐẠI',
      image: sanCau,
      bgGradient: 'from-gray-50 to-orange-50',
    },
    {
      title: 'HUẤN LUYỆN',
      subtitle: 'CHUYÊN NGHIỆP',
      description:
        'ĐỘI NGŨ HUẤN LUYỆN VIÊN CÓ CHỨNG CHỈ THỂ DỤC THỂ THAO, PHƯƠNG PHÁP ĐÀO TẠO HIỆN ĐẠI TỪ CƠ BẢN ĐẾN NÂNG CAO',
      image: hocsinh1,
      bgGradient: 'from-orange-200 to-white',
    },
  ]

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide(prev => (prev + 1) % slides.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [slides.length])

  const nextSlide = () => {
    setCurrentSlide(prev => (prev + 1) % slides.length)
  }

  const prevSlide = () => {
    setCurrentSlide(prev => (prev - 1 + slides.length) % slides.length)
  }

  return (
    <motion.section
      ref={ref}
      initial='hidden'
      animate={controls}
      variants={staggerContainer}
      className={`min-h-screen bg-gradient-to-br ${slides[currentSlide].bgGradient} relative flex items-center overflow-hidden pt-18 sm:pt-20 md:pt-22 lg:pt-24 xl:pt-26`}
    >
      <div className='relative z-10 mx-auto w-full max-w-7xl px-4'>
        <div className='grid min-h-[80vh] grid-cols-1 items-center gap-8 md:gap-12 lg:grid-cols-2'>
          {/* Text Content - Hiện trên mobile, bên trái trên desktop */}
          <motion.div
            variants={fadeInLeft}
            className='order-2 space-y-6 md:space-y-8 lg:order-1'
          >
            <motion.h1
              key={currentSlide}
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8 }}
              className='text-4xl leading-tight font-bold text-gray-900 md:text-5xl lg:text-7xl'
            >
              {slides[currentSlide].title}
              <span className='block text-orange-600'>
                {slides[currentSlide].subtitle}
              </span>
            </motion.h1>
            <motion.p
              key={`${currentSlide}-desc`}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.2 }}
              className='max-w-2xl text-lg leading-relaxed text-gray-600 md:text-xl lg:text-2xl'
            >
              {slides[currentSlide].description}
            </motion.p>
            <motion.div
              variants={fadeInUp}
              className='flex flex-col gap-4 sm:flex-row'
            >
              <button className='transform rounded-lg bg-orange-600 px-8 py-4 font-semibold text-white shadow-lg transition-all duration-300 hover:scale-105 hover:bg-orange-600 hover:shadow-xl'>
                Đặt sân ngay
              </button>
              <button className='rounded-lg border-2 border-orange-600 px-8 py-4 font-semibold text-orange-600 transition-all duration-300 hover:bg-orange-600'>
                Xem dịch vụ
              </button>
            </motion.div>

            {/* Slide Indicators */}
            <div className='flex justify-center space-x-3 pt-4 lg:justify-start'>
              {slides.map((_, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentSlide(index)}
                  className={`h-3 w-3 rounded-full transition-all duration-300 ${
                    index === currentSlide ? 'w-8 bg-orange-600' : 'bg-gray-300'
                  }`}
                />
              ))}
            </div>
          </motion.div>

          {/* Image/Icon Container - Hiện trên mobile, bên phải trên desktop */}
          <motion.div
            variants={fadeInRight}
            className='relative order-1 lg:order-2'
          >
            <motion.div
              key={`${currentSlide}-image`}
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              transition={{ duration: 0.8 }}
              className='group mx-auto aspect-[16/9] w-full max-w-lg overflow-hidden rounded-3xl shadow-2xl transition-all duration-500 lg:aspect-[16/10] lg:max-w-none'
            >
              <img
                src={slides[currentSlide].image}
                alt={`${slides[currentSlide].title} ${slides[currentSlide].subtitle}`}
                className='h-full w-full object-cover transition-transform duration-500 group-hover:scale-110'
              />
            </motion.div>

            {/* Navigation Arrows */}
            <button
              onClick={prevSlide}
              className='absolute top-1/2 left-2 flex h-10 w-10 -translate-y-1/2 items-center justify-center rounded-full bg-white/80 shadow-lg transition-all duration-300 hover:scale-110 hover:bg-white md:left-4 md:h-12 md:w-12'
            >
              <span className='text-lg text-orange-600 md:text-xl'>←</span>
            </button>
            <button
              onClick={nextSlide}
              className='absolute top-1/2 right-2 flex h-10 w-10 -translate-y-1/2 items-center justify-center rounded-full bg-white/80 shadow-lg transition-all duration-300 hover:scale-110 hover:bg-white md:right-4 md:h-12 md:w-12'
            >
              <span className='text-lg text-orange-600 md:text-xl'>→</span>
            </button>
          </motion.div>
        </div>
      </div>

      {/* Background Pattern */}
      <div className='absolute inset-0 opacity-5'>
        <div className='absolute top-20 left-10 h-20 w-20 rounded-full border border-orange-300'></div>
        <div className='absolute top-40 right-20 h-16 w-16 rounded-full border border-gray-400'></div>
        <div className='absolute bottom-40 left-20 h-24 w-24 rounded-full border border-orange-300'></div>
        <div className='absolute right-10 bottom-20 h-12 w-12 rounded-full border border-gray-400'></div>
      </div>
    </motion.section>
  )
}

