// News Section Component
const NewsSection = () => {
  const { ref, controls } = useScrollAnimation()

  const news = [
    {
      image: hocsinh,
      title: 'Khóa học cầu lông cho trẻ em',
      excerpt:
        'Chương trình đào tạo đặc biệt dành cho các em nhỏ từ 6-12 tuổi với phương pháp giảng dạy hiện đại...',
      date: '01/06/2025',
    },
    {
      image: thidau3,
      title: 'Giải đấu cầu lông Vĩnh Bảo Goodminton',
      excerpt:
        'Tham gia giải đấu với giải thưởng hấp dẫn dành cho các tay vợt nghiệp dư và chuyên nghiệp...',
      date: '25/01/2025',
    },
    {
      image: khaitruong,
      title: 'Khai trương sân cầu lông VBG',
      excerpt:
        'Mở rộng quy mô với 3 sân mới hiện đại, đáp ứng nhu cầu ngày càng tăng của khách hàng...',
      date: '28/11/2024',
    },
  ]

  return (
    <motion.section
      ref={ref}
      initial='hidden'
      animate={controls}
      variants={staggerContainer}
      className='bg-gradient-to-r from-orange-200 to-gray-200 py-16 md:py-20'
      id='news'
    >
      <div className='mx-auto max-w-7xl px-4'>
        <motion.div variants={fadeInUp} className='mb-12 text-center md:mb-16'>
          <h2 className='mb-4 text-3xl font-bold text-gray-900 md:text-4xl'>
            Tin tức nổi bật
          </h2>
          <p className='text-lg text-gray-600 md:text-xl'>
            Cập nhật những thông tin mới nhất từ Vĩnh Bảo Goodminton
          </p>
        </motion.div>

        <motion.div
          variants={staggerContainer}
          className='grid grid-cols-1 gap-6 md:grid-cols-2 md:gap-8 lg:grid-cols-3'
        >
          {news.map((article, index) => (
            <motion.div
              key={index}
              variants={fadeInUp}
              className='group transform overflow-hidden rounded-xl border border-transparent bg-gray-50 transition-all duration-300 hover:-translate-y-1 hover:border-orange-200 hover:shadow-xl'
            >
              <div className='h-56 w-full overflow-hidden'>
                <img
                  src={article.image}
                  alt={article.title}
                  className='h-full w-full object-cover transition-transform duration-300 group-hover:scale-110'
                />
              </div>
              <div className='p-6'>
                <div className='mb-2 text-sm font-medium text-orange-600'>
                  {article.date}
                </div>
                <h3 className='mb-3 text-xl font-semibold text-gray-900 transition-colors duration-300 group-hover:text-orange-600'>
                  {article.title}
                </h3>
                <p className='mb-4 leading-relaxed text-gray-600'>
                  {article.excerpt}
                </p>
                <button className='flex items-center font-semibold text-orange-600 transition-all duration-300 group-hover:translate-x-1 hover:text-orange-600'>
                  Đọc thêm
                  <span className='ml-1'>→</span>
                </button>
              </div>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </motion.section>
  )
}

