import { QueryClient } from '@tanstack/react-query'

export const createQueryClient = () => {
  return new QueryClient({
    defaultOptions: {
      queries: {
        // Caching strategy
        staleTime: 5 * 60 * 1000, // 5 phút - data được coi là fresh
        gcTime: 10 * 60 * 1000, // 10 phút - cache garbage collection
        
        // Network strategy
        retry: (failureCount, error) => {
          // Không retry cho 4xx errors
          if (error?.status >= 400 && error?.status < 500) {
            return false
          }
          // Retry tối đa 3 lần cho server errors
          return failureCount < 3
        },
        retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 30000),
        
        // Refetch strategy
        refetchOnWindowFocus: false,
        refetchOnReconnect: 'always',
        refetchOnMount: true,
        
        // Error handling
        throwOnError: false,
        
        // Suspense support
        suspense: false,
      },
      mutations: {
        // Mutation strategy
        retry: 1,
        retryDelay: 1000,
        
        // Error handling
        throwOnError: false,
        
        // Network mode
        networkMode: 'online',
      },
    },
  })
}

/**
 * Query Client instance cho toàn bộ app
 */
export const queryClient = createQueryClient()

/**
 * Query Keys Factory - Quản lý keys một cách có tổ chức
 */
export const queryKeys = {
  // Base keys
  all: ['vmu-staff-ui'],
  
  // Auth
  auth: () => [...queryKeys.all, 'auth'],
  authProfile: () => [...queryKeys.auth(), 'profile'],
  authPermissions: () => [...queryKeys.auth(), 'permissions'],
  
  // Students
  students: () => [...queryKeys.all, 'students'],
  studentsList: (filters) => [...queryKeys.students(), 'list', filters],
  studentsDetail: (id) => [...queryKeys.students(), 'detail', id],
  studentsScores: (id) => [...queryKeys.students(), 'scores', id],
  studentsWarnings: (type, filters) => [...queryKeys.students(), 'warnings', type, filters],
  
  // Surveys
  surveys: () => [...queryKeys.all, 'surveys'],
  surveysList: (filters) => [...queryKeys.surveys(), 'list', filters],
  surveysDetail: (id) => [...queryKeys.surveys(), 'detail', id],
  surveysQuestions: (id) => [...queryKeys.surveys(), 'questions', id],
  surveysResults: (id, filters) => [...queryKeys.surveys(), 'results', id, filters],
  
  // Published Surveys
  publishedSurveys: () => [...queryKeys.all, 'published-surveys'],
  publishedSurveysList: (filters) => [...queryKeys.publishedSurveys(), 'list', filters],
  publishedSurveysDetail: (id) => [...queryKeys.publishedSurveys(), 'detail', id],
  publishedSurveysStatistics: (id) => [...queryKeys.publishedSurveys(), 'statistics', id],
  
  // Notifications
  notifications: () => [...queryKeys.all, 'notifications'],
  notificationsList: (type, filters) => [...queryKeys.notifications(), type, 'list', filters],
  notificationsDetail: (id) => [...queryKeys.notifications(), 'detail', id],
  notificationsReaders: (id) => [...queryKeys.notifications(), 'readers', id],
  
  // Chatbot
  chatbot: () => [...queryKeys.all, 'chatbot'],
  chatbotDocuments: (filters) => [...queryKeys.chatbot(), 'documents', filters],
  chatbotFaq: (filters) => [...queryKeys.chatbot(), 'faq', filters],
  chatbotHistory: (filters) => [...queryKeys.chatbot(), 'history', filters],
  chatbotStatistics: (filters) => [...queryKeys.chatbot(), 'statistics', filters],
  
  // Statistics
  statistics: () => [...queryKeys.all, 'statistics'],
  statisticsSurveys: (filters) => [...queryKeys.statistics(), 'surveys', filters],
  statisticsPsychology: (filters) => [...queryKeys.statistics(), 'psychology', filters],
  statisticsSystem: (filters) => [...queryKeys.statistics(), 'system', filters],
  statisticsChatbot: (filters) => [...queryKeys.statistics(), 'chatbot', filters],
  
  // Categories
  categories: () => [...queryKeys.all, 'categories'],
  categoriesDepartments: () => [...queryKeys.categories(), 'departments'],
  categoriesMajors: (departmentId) => [...queryKeys.categories(), 'majors', departmentId],
  categoriesClasses: (majorId) => [...queryKeys.categories(), 'classes', majorId],
  categoriesSemesters: () => [...queryKeys.categories(), 'semesters'],
  categoriesSubjects: (filters) => [...queryKeys.categories(), 'subjects', filters],
}

/**
 * Cache Invalidation Utilities
 */
export const cacheUtils = {
  // Invalidate tất cả queries
  invalidateAll: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.all })
  },
  
  // Invalidate theo module
  invalidateAuth: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.auth() })
  },
  
  invalidateStudents: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.students() })
  },
  
  invalidateSurveys: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.surveys() })
  },
  
  invalidateNotifications: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.notifications() })
  },
  
  invalidateChatbot: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.chatbot() })
  },
  
  invalidateStatistics: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.statistics() })
  },
  
  invalidateCategories: () => {
    return queryClient.invalidateQueries({ queryKey: queryKeys.categories() })
  },
  
  // Remove specific queries
  removeQuery: (queryKey) => {
    return queryClient.removeQueries({ queryKey })
  },
  
  // Set query data manually
  setQueryData: (queryKey, data) => {
    return queryClient.setQueryData(queryKey, data)
  },
  
  // Get cached data
  getQueryData: (queryKey) => {
    return queryClient.getQueryData(queryKey)
  },
  
  // Prefetch data
  prefetchQuery: (queryKey, queryFn) => {
    return queryClient.prefetchQuery({
      queryKey,
      queryFn,
      staleTime: 5 * 60 * 1000,
    })
  },
} 